$breakpoint-sm: 576px;
$breakpoint-md: 768px;
$breakpoint-lg: 992px;
$breakpoint-xl: 1200px;

:root {
  --primary-color:#1AA5C3;
  --primary-font-family : Poppins;
  --default-backgroud-color: #ffffff;
  --default-button-color: #F4F4F4;
  --default-input-border-color: #CDCDCD;
  --default-seperator-color: #EBEBEB;
  --default-input-border-width: 1px;
  --default-input-border-style: solid;
  --primary-button-color: var(--primary-color); 
  --default-input-text-family: var(--primary-font-family);
  --lg-container-warpper: 0px 180px;
  --sm-container-warpper: 0px 24px;
}


html,body{
  margin: 0;
  font-family: Poppins;
  width: 100%;
  min-height: 100vh;
  background-color: var(--default-backgroud-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.body{
  margin-top: 90px;
  min-height: calc(100vh - 90px);
}


p,li,a{
  margin: 0;
  padding: 0;
}

button{
  outline: none;
}

.rounded{
  border-radius: 50%;
}

.boxed{
  border-radius: 0%;
}

.pointer{
 cursor: pointer;
}

.header-button {
  cursor: pointer;
  display: flex;
  width: 40px;
  height: 40px;
  border:none;
  background-color: #F4F4F4;
}

.main-slide{
  object-fit: cover;
  width: 100vw;
  min-width: 547px;
}

.main-slide-container{
  min-width: 547px;
  justify-content: space-between;
}

.main-slide-button{
  background-color: rgba(255, 255, 255, 0.29);
  border-radius: 50%;
  border: none;
  align-self: center;
  position: absolute;
  cursor: pointer;
  margin: 70px;
  z-index: 1000;
}

.brands-slides {
  width: calc(128px + 77px);
  height: 128px;
  padding: 0px 77px 0px 0px;
}

.empty-container{
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: center;
}

.brands-slides-button {
  background-color: rgba(248, 248, 248, 1);
  color: black;
  border-radius: 50%;
  border: none;
  align-self: center;
  position: absolute;
  cursor: pointer;
  margin: -70px;
  z-index: 1000;
}

.login{
  display: flex;
  width: 100%;
  min-height: calc(100vh - 90px);
  background-color: #F4F4F4;
}

.app {
  display: flex;
  flex-direction: column;
  width: 100%;
  position: relative;
  min-height: 100vh;
}

.logo{
  width: 134px;
  height: 48px;
}

.login-error{
  padding: 4px 0px;
  text-align: center;
}

.login-button {
  cursor: pointer;
  display: flex;
  margin: 30px auto auto auto;
  width: 176px;
  height: 52px;
  border:none;
  outline: none;
  font-family: Poppins;
  font-size: 14px;
  font-weight: 600;
  line-height: 34px;
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
  background-color: var(--primary-button-color);
}

// className:'carousole-container'
// className:'forward-button'
// className:'backward-button'

.login-input{
  height: 50px;
  width: 100%;
  margin-top: 19px;
  text-indent: 31px;
  border-width: var(--default-input-border-width);
  border-style: var(--default-input-border-style);
  border-color: var(--default-input-border-color);
}

.input-error{
  font-size: 12px;
  font-weight: 300;
  line-height: 12px;
  height: 12px;
  font-family: var(--default-input-text-family);
  color: #cf2c2c;
}



.login-title{
font-family: Poppins;
font-size: 26px;
font-weight: 600;
line-height: 52.33px;
text-align: center;
}

.login-sub-title{
  font-family: Poppins;
  font-size: 13px;
  margin-top: 24px;
  font-weight: 400;
  line-height: 13px;
  text-align: center;
}

.login-form {
  display: flex;
  flex-direction: column;
  margin: auto;
  width: 656px;
  height: 354px;
}

.header {
display: flex;
width: 100%;
z-index: 1000;
position: absolute;
padding-top: 30px;
padding-bottom: 18px;
background-color: white;
}

.footer {
  height: fit-content;
  display: flex;
