display: flex;
width: 100%;
margin: 90px 0px;
}

.product-container {
  display: flex;
  flex-direction: column;
  position: relative;
  margin: 0px 180px;
  width: 100%;

  .product-title {
    font-family: Poppins;
    font-size: 32px;
    font-weight: 600;
    line-height: 48.96px;
    text-align: left;
  }

  .product-grid {
    display: grid;
    grid-template-columns:  auto auto auto;
    grid-gap: 1px;
    background-color: #B9B9B9;
    padding: 1px;
    overflow-x: hidden;
    overflow-y: auto;
  }
  
  // .product-grid > :first-child {
