     // height: 373.02px;
     padding: 23px;
     position: relative;
     display: flex;
     cursor: pointer;
     flex-direction: column;
     border-style: none;
     background-color: var(--default-backgroud-color);
     // border: 1px 0px 0px 0px;
     // border-color: rgba(185, 185, 185, 1);
   
     .image-container{
       display: flex;
       position: relative;
       width: 100%;
       .card-image{
         width: 130.58px;
         height: 182.85px;
       }
     }
   
     .badge{
       width: 52.05px;
       transform: translate(-10px,-10px);
       height: 20.04px;
       background-color:  #44961D;   
       text-transform: uppercase;
       p{
         line-height: 20.04px;
         font-family: Plus Jakarta Sans;
         font-size: 10px;
         font-weight: 600;
         text-align: center;
         color: white;
       }
     }
   
     .card-image{
       margin: 0px auto;
       }
       
     .card-category{
       font-family: Plus Jakarta Sans;
       margin-top: 28px;
       font-size: 10px;
       font-weight: 700;
       line-height: 15px;
       letter-spacing: 1px;
       text-align: left;
       color: var(--primary-button-color);
     }
     
     .card-title{
     font-family: Poppins;
     margin-top: 10px;
     font-size: 14px;
     font-weight: 500;
     line-height: 23px;
     text-align: left;
   
       color: black;  
     }
     
     .card-price{
       display: flex;
       flex-direction: row;
       margin-top: 17px;
       justify-content: left;
       
       .inr{
       font-family: Plus Jakarta Sans;
       font-size: 12px;
       font-weight: 700;
       line-height: 15px;
       text-align: left;
       text-transform: uppercase;
       color: #606060;
       text-align: left;
       }
     
       .price{
         font-family: Plus Jakarta Sans;
         font-size: 16px;
         font-weight: 800;
         line-height: 15px;
         padding: 0px 10px;
         letter-spacing: -0.10000000149011612px;
         text-align: left;
       }
     
       .actual{
         font-family: Plus Jakarta Sans;
         font-size: 14px;
         font-weight: 600;
         line-height: 15px;
         letter-spacing: -0.10000000149011612px;
         text-decoration: line-through;
         color: #777777;
         text-align: left;
       }
     }
   
     .card-add-button{
       position: absolute;
       right: 0;
       bottom: 0;
       cursor: pointer;
       display: flex;
       width: 43px;
       height: 43px;
       transform: translateY(40px);
       background-color: #F4F4F4;
       border: solid 1px #DDDDDD;
     }
   
     .addtocart-button {
       cursor: pointer;
       display: none;
       margin: 30px auto auto auto;
       width: 176px;
       height: 52px;
       border:none;
       background-color: var(--primary-button-color);
     }
     
   }
   
   .card-small:hover{
     transform: scale(1.05);
     z-index: 100;
       -webkit-box-shadow: -1px 0px 4px -1px rgba(89,86,89,1);
     -moz-box-shadow: -1px 0px 4px -1px rgba(89,86,89,1);
     box-shadow: -1px 0px 4px -1px rgba(89,86,89,1);
     border: 1px solid var(--default-input-border-color);
   }
   
   .brands-container {
     // background-color: rgb(67, 115, 115);
     margin:  var(--sm-container-warpper);
     
   }
   
   .brands-title {
     font-family: Poppins;
     font-size: 32px;
     font-weight: 600;
     line-height: 48.96px;
     text-align: left;
   }
   

}

